"use strict";
const STOP = {};
function walkTree(node, fn, state) {
  state = fn(node, state);
  if (state === STOP)
    return;
  const {children} = node;
  if (!children)
    return;
  for (const child of children) {
    walkTree(child, fn, state);
  }
}
walkTree.STOP = STOP;
module.exports = walkTree;
