"use strict";
const {toArray} = require("react").Children;
const {last} = require("lodash");
const {TYPE_SUSPENSE, TYPE_PROMISE} = require("./constants.js"), {NO_SSR} = require("./symbols.js"), {followAndAbort} = require("./abort.js"), walkTree = require("./walkTree.js");
module.exports = {
  handleInterrupt() {
    const {interrupt} = this;
    this.interrupt = null;
    const {type} = interrupt;
    if (type === TYPE_SUSPENSE) {
      this.handleSuspense(interrupt.element);
    } else {
      this.handlePromise(interrupt.element, interrupt.promise);
    }
  },
  handleSuspense(element) {
    const {fallback} = element.props;
    if (fallback === void 0)
      return;
    const frame = last(this.stack);
    const node = this.createNodeWithStackState(TYPE_SUSPENSE, frame);
    node.fallback = fallback;
    node.suspended = false;
    const parentSuspense = this.suspenseNode;
    node.suspendedAbove = parentSuspense ? parentSuspense.suspended || parentSuspense.suspendedAbove : false;
    node.containsLazy = false;
    this.suspenseNode = node;
    this.node = node;
    node.frame = frame;
  },
  handlePromise(element, promise) {
    const {suspenseNode} = this;
    if (!suspenseNode) {
      followAndAbort(promise);
      throw new Error("A React component suspended while rendering, but no fallback UI was specified.\n\nAdd a <Suspense fallback=...> component higher in the tree to provide a loading indicator or placeholder to display.");
    }
    const {stack} = this;
    const frame = this.stackPopOriginal.call(stack);
    if (!suspenseNode.containsLazy)
      suspenseNode.containsLazy = true;
    if (suspenseNode.suspendedAbove && !suspenseNode.suspended)
      suspenseNode.suspended = true;
    if (suspenseNode.suspended) {
      followAndAbort(promise);
      if (!this.fallbackFast) {
        const node2 = this.createNode(TYPE_PROMISE);
        node2.resolved = true;
        node2.promise = promise;
      }
      return;
    }
    if (promise[NO_SSR]) {
      this.handleNoSsrPromise(promise);
      return;
    }
    let node;
    promise.then(() => {
      if (!node) {
        node = {resolved: true};
        return;
      }
      this.resolved(node);
    }, (err) => {
      if (!node) {
        node = {resolved: true};
        throw err;
      }
      this.rejected(node, err);
    });
    if (node) {
      node = this.createNode(TYPE_PROMISE);
      node.resolved = true;
      node.promise = promise;
      node.frame = frame;
      this.node = node;
      frame.children = toArray(element);
      stack.push(frame);
      return;
    }
    node = this.createNodeWithStackState(TYPE_PROMISE, frame);
    node.resolved = false;
    node.promise = promise;
    node.element = element;
    this.numAwaiting++;
  },
  resolved(node) {
    if (node.resolved)
      return;
    node.resolved = true;
    this.numAwaiting--;
    this.rerender(node, node.element);
  },
  rejected(node, err) {
    if (node.resolved)
      return;
    node.resolved = true;
    node.stackState = null;
    this.numAwaiting--;
    this.errored(err);
  },
  handleNoSsrPromise(promise) {
    followAndAbort(promise);
    if (!this.fallbackFast) {
      const node = this.createNode(TYPE_PROMISE);
      node.resolved = true;
      node.promise = promise;
    }
    const {suspenseNode} = this;
    suspenseNode.suspended = true;
    const suspenseFrame = suspenseNode.frame;
    if (!suspenseFrame) {
      this.fallbacksQueue.push(suspenseNode);
    } else if (this.fallbackFast) {
      const {stack} = this;
      let stackIndex = stack.length - 1;
      while (true) {
        const frame = stack[stackIndex];
        frame.childIndex = frame.children.length;
        if (frame === suspenseFrame)
          break;
        stackIndex--;
      }
    }
    for (const child of suspenseNode.children) {
      this.suspendDescendents(child);
    }
  },
  suspendDescendents(node) {
    walkTree(node, (node2, inLazy) => {
      const {type} = node2;
      if (type === TYPE_SUSPENSE) {
        if (node2.suspended)
          return walkTree.STOP;
        if (!inLazy && !this.fallbackFast) {
          if (node2.containsLazy) {
            node2.suspended = true;
            this.fallbacksQueue.push(node2);
          } else {
            node2.suspendedAbove = true;
          }
        }
      } else if (type === TYPE_PROMISE) {
        if (!node2.resolved)
          this.abort(node2);
        inLazy = true;
      }
      return inLazy;
    }, false);
  }
};
