"use strict";
const {TYPE_TEXT, TYPE_PROMISE, TYPE_FALLBACK} = require("./constants.js"), {ON_MOUNT} = require("./symbols.js");
class Walker {
  constructor(tree, makeStaticMarkup, fallbackFast) {
    this.tree = tree;
    this.makeStaticMarkup = makeStaticMarkup;
    this.fallbackFast = fallbackFast;
    this.out = "";
    this.lastText = false;
  }
  walk() {
    this.process(this.tree, false);
    return this.out;
  }
  process(node, suspended) {
    const {type} = node;
    if (type === TYPE_TEXT) {
      if (!suspended) {
        const {out} = node;
        if (this.lastText && out.slice(0, 1) !== "<")
          this.out += "<!-- -->";
        this.out += out;
        if (!this.makeStaticMarkup)
          this.lastText = out.slice(-1) !== ">";
      }
      return;
    }
    if (type === TYPE_PROMISE) {
      const {promise} = node;
      if (typeof promise[ON_MOUNT] === "function")
        promise[ON_MOUNT](!suspended);
      if (suspended)
        return;
    } else if (type === TYPE_FALLBACK && !this.fallbackFast) {
      for (const child of node.suspendedChildren) {
        this.process(child, true);
      }
    }
    for (const child of node.children) {
      this.process(child, suspended);
    }
  }
}
function treeToHtml(tree, makeStaticMarkup, fallbackFast) {
  const walker = new Walker(tree, makeStaticMarkup, fallbackFast);
  return walker.walk();
}
module.exports = treeToHtml;
