"use strict";
const {ABORT} = require("./symbols.js");
function abort(promise) {
  if (typeof promise[ABORT] === "function")
    promise[ABORT]();
}
function followAndAbort(promise) {
  let resolved = false;
  const resolve = () => {
    resolved = true;
  };
  promise.then(resolve, resolve);
  if (!resolved)
    abort(promise);
}
module.exports = {
  abort,
  followAndAbort
};
